.search-container {
    padding-top: 20px;
}

.search-container .form-control {
    padding-left: 2.375rem;
}

.search-container .form-control-feedback {
    position: absolute;
    z-index: 2;
    display: block;
    width: 2.375rem;
    height: 2.375rem;
    line-height: 2.375rem;
    text-align: center;
    pointer-events: none;
    color: #aaa;
}